<template>
  <el-table
    v-loading.body="loading"
    :data="tableData"
    style="width: 100%">
    <el-table-column
      prop="date"
      label=""
      width="180">
    </el-table-column>
    <el-table-column
      prop="name"
      label=""
      width="180">
    </el-table-column>
    <el-table-column
      prop="address"
      label="">
    </el-table-column>
  </el-table>
</template>

<style>
  body {
    margin: 0;
  }
</style>

<script>
  export default {
    data() {
      return {
        tableData: [{
          date: '2016-05-03',
          name: '',
          address: ' 1518 '
        }, {
          date: '2016-05-02',
          name: '',
          address: ' 1518 '
        }, {
          date: '2016-05-04',
          name: '',
          address: ' 1518 '
        }],
        loading: true
      };
    }
  };
</script>


